import { lazy } from 'react'

const AppRoutes = [
  {
    path: '/apps/todo',
    exact: true,
    appLayout: true,
    className: 'todo-application',
    component: lazy(() => import('../../views/apps/todo'))
  },
  {
    path: '/apps/todo/:filter',
    appLayout: true,
    exact: true,
    className: 'todo-application',
    component: lazy(() => import('../../views/apps/todo')),
    meta: {
      navLink: '/apps/todo'
    }
  },
  {
    path: '/apps/post-task/list',
    component: lazy(() => import('../../views/apps/invoice/list'))
  },
  {
    path: '/apps/post-task/list-category/:category',
    component: lazy(() => import('../../views/apps/invoice/list-category'))
  },
  {
    path: '/apps/post-task/detail/:id',
    component: lazy(() => import('../../views/apps/invoice/detail')),
    meta: {
      navLink: '/apps/post-task/detail'
    }
  },
  {
    path: '/apps/task/list',
    className: 'ecommerce-application',
    component: lazy(() => import('../../views/apps/ecommerce/task'))
  },
  {
    path: '/apps/package/list',
    className: 'ecommerce-application',
    component: lazy(() => import('../../views/apps/ecommerce/package'))
  },
  {
    path: '/apps/task/buy-job',
    className: 'ecommerce-application',
    component: lazy(() => import('../../views/apps/ecommerce/buy-job/'))
  },
  {
    path: '/apps/task/buyjob-detail/:id',
    className: 'ecommerce-application',
    component: lazy(() => import('../../views/apps/ecommerce/buy-detail/'))
  },
  {
    path: '/apps/task/posted-job',
    className: 'ecommerce-application',
    component: lazy(() => import('../../views/apps/ecommerce/posted-job/'))
  },
  {
    path: '/apps/task/posted-detail/:id',
    className: 'ecommerce-application',
    component: lazy(() => import('../../views/apps/ecommerce/posted-detail/'))
  },

  {
    path: '/apps/task/checkout',
    className: 'ecommerce-application',
    component: lazy(() => import('../../views/apps/ecommerce/checkout'))
  }
]

export default AppRoutes
