const BaseUrl = 'https://adworks.app/api/'

// history = useHistory()
// ** GET Products
export const getProducts = (tasks, params, totalTasks) => {
  return dispatch => {
    dispatch({ type: 'GET_PRODUCTS', tasks, params, totalTasks })
  }
}

// ** Add Item to Cart
export const addToCart = (taskTemp, cartTemp) => {
  return (dispatch, getState) => {
    dispatch({ type: 'ADD_TO_CART', taskTemp, cartTemp })
  }
}

// ** DELETE Cart Items
export const deleteCartItem = (cartTemp, taskTemp) => {
  return dispatch => {
    dispatch({ type: 'DELETE_CART_ITEM', cartTemp, taskTemp })
  }
}

