export const selectBuyTasks = (data, userData) => {
  return dispatch => {
    return axios.post(`${BaseUrl}jobs/selectBuyTasks`, { data, userData }).then(res => {
      // console.log(res)
      if (res.data.status === true) {
        toast.success("You have successfully purchased the selected work!")
        dispatch({
          type: 'ALLCART_DELETE'
        })
        window.location.href = '/apps/task/list'
      }
    })
  }
}
export const getBuyTasks = (userData) => {
  return dispatch => {
    return axios.post(`${BaseUrl}jobs/getBuyTasks`, userData).then(res => {
      dispatch({ type: 'GET_BUY_TASK', data: res.data, allData: res.allData, total: res.total })
    })
  }
}
export const getBuyTaskDetail = (id) => {
  return dispatch => {
    return axios.post(`${BaseUrl}jobs/getBuyTaskDetail`, { buyTaskId: id }).then(res => {
      dispatch({ type: 'GET_BUY_TASK_DETAIL', detail: res.data.data })
    })
  }
}
export const postTask = (postData) => {
  return dispatch => {
    return axios.post(`${BaseUrl}jobs/postTask`, postData).then(res => {
      if (res.data.status === true) {
        toast.success("You have successfully posted your work !")
        // history.push('/app/task/buyjob')
        window.location.href = '/apps/task/buy-job'
      }
    })
  }
}
export const getTaskInPackage = (data) => {
  return dispatch => {
    return axios.post(`${BaseUrl}/jobs/getTaskInPackage`, data).then(res => {
      dispatch({ type: 'ALLPACKAGE_WITH_TASK', allData: res.data.data, totalNum: res.data.totalNum, params: data })
    })
  }
}
export const buyPackage = (title, userId) => {
  return dispatch => {
    return axios.post(`${BaseUrl}/jobs/buyPackage`, { title, userId }).then(res => {
      if (res.data.status === true) {
        toast.success("You have successfully purchased the selected package!")
      }
    })
  }
}